import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { TypeOfRationalComponent } from './type-of-rational.component';

const routes: Routes = [
  {
    path : '',
    component : TypeOfRationalComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TypeOfRationalRoutingModule { }
