import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatTableDataSource } from '@angular/material/table';
import { FormListComponent } from '@fragments';
import { ApiService, SweetAlertService } from '@services';
import { COMMON_FUNCTION, ENDPOINTS, STATUSES } from '@shared';
import { TypeOfRational } from 'app/core/models/type-of-rational.model';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-type-of-rational',
  templateUrl: './type-of-rational.component.html',
  styleUrls: ['./type-of-rational.component.scss']
})
export class TypeOfRationalComponent implements OnInit {

  @ViewChild(FormListComponent) formListComponent: FormListComponent;
  dataSource: MatTableDataSource<TypeOfRational>;
  displayedColumns: string[] = ['rationale', 'rationaleCategory', 'status'];;
  statusList1 = ['FOR_COMPLIANCE', 'FOR_INCOMPLETE_DECISION', 'FOR_TOA_APPLICATION'];
  statusList = [...STATUSES];

  typeOfRational = new TypeOfRational();
  typeOfRationalList = [];

  typeOfRationalForm: FormGroup;

  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private sweetAlertService: SweetAlertService
  ) {

  }

  ngOnInit(): void {
    this.getAllTypeOfRational();
    this.setFormValidator();
  }


  setFormValidator(): void {
    this.typeOfRationalForm = this.formBuilder.group({
      rationale: ['', [Validators.required]],
      rationaleCategory: ['', [Validators.required]],
      // status: ['', [Validators.required]],
    })
  }

  resetFormValidator(): void {
    this.typeOfRational = new TypeOfRational();
    this.typeOfRational.status = COMMON_FUNCTION.ACTIVE;
    this.typeOfRationalForm.markAsUntouched();
    this.typeOfRationalForm.markAsPristine();
  }

  getAllTypeOfRational(): void {
    this.apiService.findAll(ENDPOINTS.typeOfRationale).subscribe((res: any) => {
      console.log(res.responseData.data);
      this.typeOfRationalList = res.responseData.data;
      this.dataSource = new MatTableDataSource<TypeOfRational>(res.responseData.data);
      this.dataSource.paginator = this.formListComponent.paginator;
      this.dataSource.sort = this.formListComponent.sort;
    });
  }

  getTypeOfRational(): void {
    this.apiService.findById(ENDPOINTS.typeOfRationale, this.formListComponent.idForUpdate).subscribe((res: any) => {
      if (res) {
        this.typeOfRational = res.responseData.data;
      }
    });
  }

  saveTypeOfRational(): void {
    this.apiService.save(ENDPOINTS.typeOfRationale, this.typeOfRational).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {

        this.resetFormValidator();
        this.getAllTypeOfRational();
      }
    )
  }

  updateTypeOfRational(): void {
    this.apiService.update(ENDPOINTS.typeOfRationale, this.typeOfRational).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {

        this.resetFormValidator();
        this.getAllTypeOfRational();
      });
  }

  deleteTypeOfRational(): void {

    Swal.fire({
      text: 'Are you sure you want to delete this TYPE OF RATIONALE?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.apiService.deleteById(ENDPOINTS.typeOfRationale, this.typeOfRational.id).subscribe((res: any) => {
          console.log(res.responseData.data);
          this.sweetAlertService.customSuccessMessage('Type of rationale successfully deleted.');
          this.resetFormValidator(),
            this.getAllTypeOfRational();
        });
      }
    });
  }

}
